<script>
import bus from "@/bus.js";
import $ from "jquery";
import {
  Dropdown,
  DropdownItem,
  DropdownMenu,
  Table,
  TableColumn,
} from "element-ui";
export default {
  components: {
    InlineActionsTable: () => import("./Finances/RegularTables/InlineActionsTable"),
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    SearchBarWithAvatar: () => import("./components/layout/SearchBarWithAvatar.vue"),
  },
  data() {
    return {
      showPreview: false,
      previewData: {},
      studioName: "Octave Music Studio House",
      address: "657 W Lake St, Chicago",
      phone: "+1 400 820 8820",
    };
  },
  methods: {
    preview(data) {
      let self = this;
      self.previewData = data;
      // console.log("preview: ", self.previewData);
      self.showPreview = true;
    },
    hidePreview() {
      this.showPreview = false;
      this.previewData = {};
    },
    numberToPercent(number) {
      return this.$tools.numberToPercent(number);
    },
    calculateSalesTax() {
      let self = this;
      let subTotal = self.previewData.data.subTotal;
      let salesTax = self.previewData.data.salesTax;
      return (parseFloat(subTotal) * parseFloat(salesTax)).toFixed(2);
    },
  },
};
</script>
